//! Color 
$primary-color:#fff;
$secondary-color:#000;
$border-color:#BABBC5;
$gray-color:#6C7279;
$active-color:#E71414;

$header-background-color:#252B49;
$button1-background-color:#FF4343;
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
ul,ol {
    margin: 0;
    padding: 0;
    list-style: none;
}
a {
    text-decoration: none;
}
body {
    font-family: Raleway, Arial, sans-serif;
    display: flex;
    flex-flow: column wrap;
}
.button {
    text-transform: uppercase;
    cursor: pointer;
    color: $primary-color;
    font-size: 24px;
    font-family: Raleway, Arial, serif;
    font-weight: 600;
    border: none;
}
.button--red {
    background-color: $button1-background-color;
}
.header {
    min-height: 747px;
    background-image: url(../img/banner-background.jpg);
    background-color: $header-background-color;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
}
.header-nav {
    max-width: 1170px;
    margin: 0 auto;
    margin-bottom: 130px;
&__list {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    border-bottom: 2px solid $border-color;
}
&__item {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    min-height: 125px;
}
&__item:not(:last-child) {
    margin-right: 15px;
}
&__link {
    font-size: 13px;
    text-transform: uppercase;
    padding: 10px 20px;
    color: $primary-color;
}
}

.header-nav__link:hover,
.header-nav__link:hover {
    pointer-events: auto;
    transition: border ease-in-out .2s;
    border-bottom: 3px solid $border-color;
}
.headaer-nav__item--active {
    position: relative;
}
.header-nav__link--active {
    cursor: default;
    color: $active-color;
}
.header-nav__link--active::after {
    position: absolute;
    width: 100%;
    content: "";
    bottom: 0px;
    left: 0px;
    border-bottom: 3px solid $active-color;
}
.banner {
    width: 1170px;
    margin: 0 auto;
    color: $primary-color;
    text-align: center;
}
.banner__title {
    font-size: 64px;
    margin-bottom: 30px;
    font-weight: 600;
    text-transform: uppercase;
}
.banner__text {
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 80px;
}
.banner__button {
    padding: 20px 90px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.250);
    margin-bottom: 120px;
}
.main {
    flex-shrink: 0;
}

.feature {
    padding: 110px 170px;
}
.feature__list {
    width: 1170px;   
    display: flex;
    flex-flow: row wrap;
    align-items: center;
}   
.feature__item {
    max-width: 370px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    margin-bottom: 90px;
}
.feature__img {
    margin-bottom: 30px;
}
.feature__title {
    padding: 15px 20px;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-size: 24px;
    font-weight: 800;
}
.feature__text {
    color: $gray-color;
    font-size: 18px;
    text-align: center;
}
.footer {
    background-color: azure;
    min-height: 200px;
}